import React, { useState } from 'react'
import { styled } from '@material-ui/core/styles'
import { Draggable } from 'react-beautiful-dnd'

import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Input from '@material-ui/core/Input'
import Fab from '@material-ui/core/Fab'

const MyFab = styled(Fab)({
  boxShadow: 'none'
})

const Todo = ({ id, text, cardIndex, todoIndex, onDelete, editing, onEdit }) => {

  const [todoText, setTodoText] = useState(text)

  const html = editing ?
    <Input 
      onChange={e => setTodoText(e.target.value)} 
      value={todoText}
      autoFocus 
    />
    :
    <ListItemText primary={text} />

  return (
    <Draggable draggableId={id} index={todoIndex}>
      {(provided) => (
        <ListItem 
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          innerRef={provided.innerRef}
          index={todoIndex} 
          onKeyPress={e => {
            if(e.key === 'Enter' && todoText.trim() !== 0) {
              onEdit(cardIndex, todoIndex, todoText)
            }
          }}
        >
          <ListItemText primary={`${todoIndex + 1}.`} />
          { html }
          <MyFab 
            size="small" 
            color="secondary"
            onClick={() => onEdit(cardIndex, todoIndex, todoText)}
          >
            <i className="material-icons">
              edit
            </i>
          </MyFab>
          <MyFab 
            size="small"
            onClick={() => onDelete(cardIndex, todoIndex)}
          > 
            <i className="material-icons">
              delete
            </i>  
          </MyFab>
        </ListItem>
      )}
    </Draggable>
  )
}

export default Todo
